.AppointmentLayout {
  &-area {
    padding-bottom: 110px;
    @media screen and (max-width: 480px) {
      margin: 0;
    }
  }
  &-header {
    margin: 28px 0 26px;
    min-height: 36px;
    display: flex;
    justify-content: space-between;
    @media screen and (max-width: 480px) {
      margin: 20px;
    }
    & > button {
      & > i {
        margin-right: 10px;
        font-size: 20px;
      }
      &:nth-child(2) > i {
        margin: 0 0 0 10px;
      }
    }
  }
  &-wrapperContainer {
    padding: 40px 0;
    background: var(--petrol-5);
    border: 1px solid #fff;
    border-radius: 30px;
    box-shadow: 5px 0px 20px rgba(21, 34, 79, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
    @media screen and (max-width: 860px) {
      padding: 30px 50px;
    }
    @media screen and (max-width: 640px) {
      padding: 30px 20px;
    }
  }
  &-container {
    width: 100%;
    max-width: calc(70% - 20px);
    @media screen and (max-width: 1200px) {
      max-width: 80%;
    }
    @media screen and (max-width: 1024px) {
      max-width: 85%;
    }
    @media screen and (max-width: 860px) {
      max-width: initial;
    }
  }
  &-title {
    margin: 30px 0;
    color: var(--nightblue-100);
    font-size: 27px;
    line-height: 31px;
    text-align: center;
    letter-spacing: -0.01em;
    word-break: break-word;
    @media screen and (max-width: 860px) {
      margin: 30px 0;
      text-align: left;
    }
  }
  &-footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 90px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 8px 6px 4px var(--petrol-100);
    & > button {
      max-width: 325px;
      height: 45px !important;
      background: var(--petrol-80) !important;
      color: #fff !important;
      font-size: 15px;
      &:disabled {
        border-width: 2px !important;
      }
      &:focus {
        border-width: 2px !important;
        border-color: var(--petrol-80) !important;
      }
      @media screen and (max-width: 480px) {
        max-width: calc(100% - 30px);
      }
    }
  }
  &-workbooks {
    margin: 0;
    padding: 0;
    background: transparent;
    border-radius: 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    & .hb-workbook-item {
      width: calc(50% - 15px);
      height: auto;
      background: #fff;
      &:first-child,
      &:nth-child(2) {
        margin-top: 0;
      }
      &.high,
      &:hover {
        color: #fff;
        background: var(--petrol-80);
        & .icon,
        & .arrow {
          background: var(--petrol-60);
        }
      }
      @media screen and (max-width: 860px) {
        width: 100%;
        &:nth-child(2) {
          margin-top: 8px;
        }
      }
      & .label {
        white-space: pre-line;
        text-overflow: initial;
        word-break: break-word;
      }
    }
  }
  &-subLabel {
    margin-bottom: 15px;
    color: var(--nightblue-100);
    font-size: 15px;
  }
}

/**
* APPOINTMENT TOPICS
*/
.AppointmentTopics {
  &-inputSearch {
    padding: 0 20px;
    height: 48px;
    background: #fff;
    border-radius: 30px;
    display: flex;
    align-items: center;
    & > input {
      margin-left: 12px;
      padding: 0 !important;
      color: var(--petrol-100) !important;
      font-size: 13px;
      font-weight: bold;
      border: none;
      outline: none;
      box-shadow: none !important;
      opacity: 0.75;
      order: 2;
    }
    & > i {
      color: var(--petrol-100) !important;
      font-size: 22px;
      font-weight: bold;
      position: relative !important;
      top: initial !important;
      left: initial !important;
      display: inline-block;
      order: 1;
    }
  }
  &-noResults {
    margin: 30px 0 0;
    color: var(--petrol-100);
    font-weight: 500;
    text-align: center;
  }
}

.AppointmentTopicsSuggest {
  &-container {
    position: absolute;
    left: 0;
    top: calc(100% + 2px);
    width: 100%;
    background: #fff;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2);
  }
  &-list {
    padding: 15px 0;
    margin: 0;
    list-style-type: none;
    & > li {
      padding: 5px 15px;
      height: 45px;
      color: var(--nightblue-100);
      font-weight: 500;
      display: flex;
      align-items: center;
      cursor: pointer;
      border-bottom: 1px solid var(--petrol-5);
      transition: all 0.1s ease-in-out;
      &:last-child {
        border-bottom: none;
      }
      & > .icon {
        margin-right: 15px;
        width: 35px;
        height: 35px;
        background: var(--petrol-5);
        border-radius: 12px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      & > .text {
        max-width: calc(100% - 50px);
      }
      &:hover {
        background: var(--petrol-60);
        & > .text {
          color: #fff;
        }
      }
    }
  }
}

/**
* EMERGENCY AID
*/
.EmergencyAid {
  &-container {
    & > p {
      margin: 0;
      color: var(--nightblue-100);
      font-size: 17px;
      line-height: 23px;
    }
  }
  &-lb {
    margin: 30px 0 0;
    color: var(--nightblue-100);
    font-size: 19px;
    font-weight: 500;
    letter-spacing: -0.01em;
  }
}

/**
* ADVICE FOR WHOM
*/
.AdviceForWhom {
  &-actions {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &-actions > div,
  &-btn {
    width: calc(50% - 15px);
    @media screen and (max-width: 860px) {
      width: 100%;
      &:first-child {
        margin-bottom: 16px;
      }
    }
    & > button {
      padding: 0;
      width: 100%;
      height: 45px;
      font-size: 15px;
      border-radius: 12px;
    }
  }
}
.AddRelativesForm {
  &-form {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    & .HBFormGroup {
      &-container {
        margin: 30px 0 0;
        width: calc(50% - 15px);
        &:first-child,
        &:nth-child(2) {
          margin-top: 0;
        }
        @media screen and (max-width: 860px) {
          margin: 24px 0 0;
          width: 100%;
          &:nth-child(2) {
            margin-top: 24px;
          }
        }
      }
    }
  }
}
.ListAdviceRelatives {
  &-area {
    & .AdviceForWhom-btn {
      @media screen and (max-width: 860px) {
        margin-top: 16px;
      }
    }
    & > .empty {
      & .AdviceForWhom-btn {
        width: 100%;
      }
    }
  }
}

.AdviceRelativeItem {
  &-container {
    width: calc(50% - 15px);
    position: relative;
    @media screen and (max-width: 860px) {
      margin-top: 8px;
      width: 100%;
      &:first-child {
        margin-top: 0;
      }
    }
    & > .hb-workbook-item {
      width: 100% !important;
      &:hover {
        color: var(--nightblue-100) !important;
        background: #fff;
        & .icon,
        & .arrow {
          background: var(--petrol-5);
        }
      }
      & .label {
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }
  }
  &-options {
    display: none;
    position: absolute;
    min-width: 135px;
    top: -20px;
    left: calc(100% + 6px);
    z-index: 1;
    &.open {
      display: inline-flex;
    }
    &:after {
      content: '';
      position: absolute;
      top: 24px;
      left: -6px;
      width: 0;
      height: 0;
      border-top: 8px solid transparent;
      border-bottom: 8px solid transparent;
      border-right: 6px solid white;
    }
    @media screen and (max-width: 1200px) {
      top: calc(100% + 8px);
      left: auto;
      right: 0;
      &:after {
        top: -11px;
        left: auto;
        right: 24px;
        transform: rotateZ(90deg);
      }
    }
  }
  &-menu {
    margin: 0;
    list-style-type: none;
    display: inline-flex;
    flex-direction: column;
    background: #fff;
    border: 1px solid #fff;
    border-radius: 16px;
    box-shadow: 0 1px 8px rgba(153, 153, 153, 0.3);
    overflow: hidden;
    & > li {
      padding: 16px;
      color: var(--petrol-80);
      font-size: 13px;
      font-weight: 500;
      border-bottom: 1px solid var(--petrol-5);
      cursor: pointer;
      &:last-child {
        border-bottom: none;
      }
      &:hover {
        background: #f1f2f3;
      }
      & > i {
        font-size: 24px;
        vertical-align: middle;
      }
    }
  }
}

/**
* SURVEY
*/
.SurveyQuestionItem {
  &-container {
    margin-bottom: 30px;
    padding-bottom: 30px;
    border-bottom: 2px solid var(--petrol-10);
    &:last-child {
      margin-bottom: 0;
      padding-bottom: 0;
      border-bottom: none;
    }
    & .ListInputRadiosVertical {
      &-container {
        justify-content: center;
        @media screen and (max-width: 860px) {
          justify-content: space-between;
        }
        & .InputRadioVertical-container {
          @media screen and (max-width: 860px) {
            margin-right: 0;
            width: calc(50% - 10px);
          }
          @media screen and (max-width: 360px) {
            height: 130px;
          }
        }
      }
    }
    & .ListInputRadios {
      &-container {
        justify-content: space-between;
        & .InputRadio-container {
          margin: 16px 0 0;
          width: calc(50% - 15px);
          &:first-child,
          &:nth-child(2) {
            margin-top: 0;
          }
          @media screen and (max-width: 860px) {
            width: 100%;
            &:nth-child(2) {
              margin-top: 16px;
            }
          }
        }
      }
    }
    @media screen and (max-width: 480px) {
      .InputTextArea-area {
        height: 150px;
      }
    }
  }
}

/**
* Schedule
*/
.Schedule {
  &-lb {
    margin-bottom: 8px;
    color: var(--nightblue-100);
    font-size: 13px;
  }
  &-chooseDate {
    margin-bottom: 15px;
    padding-bottom: 15px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    border-bottom: 1px solid var(--petrol-10);
    @media screen and (max-width: 860px) {
      margin-bottom: 30px;
      padding-bottom: 30px;
    }
    & > div {
      width: calc(50% - 15px);
      @media screen and (max-width: 860px) {
        width: 100%;
      }
    }
  }
  &-datePicker {
    @media screen and (max-width: 860px) {
      margin-top: 30px;
    }
    & .FormGroupDatePicker {
      &-area {
        margin: 0;
      }
      &-container {
        & > input {
          height: 51px;
        }
        & > .hb-datepicker {
          width: 51px;
          height: 51px;
        }
      }
    }
  }
  &-timesOptions {
    display: flex;
    align-items: center;
    & > div {
      margin-bottom: 0;
      flex-grow: 1;
    }
    & > span {
      margin: 0 16px;
      color: var(--nightblue-100);
      font-size: 13px;
    }
  }
  &-options {
    & > button {
      height: 51px;
      font-size: 17px;
      border-radius: 16px;
    }
  }
}

/**
* ALMOST DONE
*/
.AlmostDone {
  &-desc {
    margin-bottom: 30px;
    color: var(--nightblue-100);
    font-size: 18px;
    letter-spacing: -0.02em;
    text-align: center;
    word-break: break-word;
    @media screen and (max-width: 860px) {
      text-align: left;
    }
  }
  &-gender {
    padding-bottom: 15px;
    border-bottom: 1px solid var(--petrol-10);
  }
  &-form {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    & .HBFormGroup {
      &-container {
        margin: 15px 0 0;
        width: calc(50% - 15px);
        @media screen and (max-width: 860px) {
          width: 100%;
          &.tablet {
            width: calc(50% - 15px);
          }
        }
        @media screen and (max-width: 480px) {
          width: 100% !important;
        }
      }
    }
  }
}

/**
* COST
*/
.Cost {
  &-container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    & > div {
      width: calc(50% - 15px);
      @media screen and (max-width: 860px) {
        width: 100%;
        &:first-child {
          margin-bottom: 15px;
        }
      }
    }
    & p {
      margin: 0;
      color: var(--nightblue-100);
      font-size: 17px;
      line-height: 23px;
    }
  }
  &-price {
    color: var(--nightblue-100);
    font-size: 34px;
  }
  &-rightContent {
    & .EmergencyAid-lb {
      &:first-child {
        margin-top: 0;
      }
    }
    & .link {
      color: var(--violet-80);
    }
  }
}

/**
* OVERVIEW
*/
.Overview {
  &-desc {
    margin-bottom: 30px;
    padding: 30px 0;
    color: var(--nightblue-100);
    border-top: 1px solid var(--petrol-10);
    border-bottom: 1px solid var(--petrol-10);
    & > h5 {
      font-size: 19px;
      font-weight: 500;
      letter-spacing: -0.01em;
    }
    & > p {
      margin: 0;
      font-size: 17px;
      letter-spacing: -0.02em;
    }
  }
  &-form {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    & .HBFormGroup {
      &-container {
        margin: 15px 0 0;
        width: calc(50% - 15px);
        @media screen and (max-width: 860px) {
          width: 100%;
        }
      }
    }
  }
  &-options {
    & > button {
      &:not(.light) {
        display: none;
      }
    }
  }
}

/**
* FINISH
*/
.Finish {
  &-container {
    text-align: center;
    & p {
      margin-bottom: 0;
      color: var(--nightblue-100);
      font-size: 18px;
      text-align: center;
      &.line2 {
        margin-top: 30px;
        padding: 30px 0;
        font-size: 19px;
        border-top: 1px solid var(--petrol-10);
        border-bottom: 1px solid var(--petrol-10);
      }
      @media screen and (max-width: 860px) {
        text-align: left;
        &.line2 {
          font-size: 16px;
        }
      }
      @media screen and (max-width: 480px) {
        font-size: 17px !important;
      }
    }
    & > img {
      margin-top: 15px;
      width: 200px;
      height: 200px;
      object-fit: cover;
    }
  }
}
